import { Avatar } from '@mui/material';
import './app-order-report-view.scss';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import { useSelector } from 'react-redux';
import { RootReducer } from '../../../_models/roo-reducer-model';
import { Attachments } from '../../../_models/report';
import PopoverComponent, { Options } from '../app-popover/popover-component';
import { useState } from 'react';
import AppModelService from '../../../_service/app-model-service';
import ProfileDetails from '../app-profile-details/profile-details';
import AppOrderDetail from '../app-order-detail/app-order-detail';
import AppShipmentTrack from '../app-shipment-track/app-shipment-track';
import AppInitiateRefund from '../app-initiate-refund/app-initiate-refund';

interface IOrderDetail {
    trackShipmentClick: () => void;
    initiateRefundClick: () => void;
}

const AppOrderReportView = (props: IOrderDetail) => {
    const detailedObj: any = useSelector((state: RootReducer) => state.flaggedReport.data);
    const { user, query, reportedUser, post, actionList, attachments, detail } = detailedObj;
    const [popoverOpen, setPopoverOpen] = useState(false);
    const [pAnchorEl, setpAnchorEl] = useState<EventTarget | any>(null);
    const configData = [{ id: 1, text: 'View profile' }, { id: 2, text: 'Show Order Details' }]
    const [shipmentPreview, setShipmentPreview] = useState(false);

    const openAction = (event: React.MouseEvent<HTMLElement>) => {
        setPopoverOpen(true);
        setpAnchorEl(event.currentTarget);
    }

    function showPreviewPopup(attachments: Attachments[]) { }

    const closePopover = () => {
        setPopoverOpen(false);
    }

    const selectedData = (ev: Options) => {
        switch (ev.id) {
            case 1:
                AppModelService.createPopUp({
                    compoent: <ProfileDetails id={user.id} />,
                    headerName: '',
                    header: false,
                    footer: false,
                    width: 580,
                    height: 560,
                    class: 'main_content',
                    showCloseIcon: true
                });
                break;
            case 2:
                AppModelService.createPopUp({
                    compoent: <AppOrderDetail />,
                    headerName: '',
                    header: false,
                    footer: false,
                    width: 820,
                    height: 760,
                    class: 'main_content',
                    showCloseIcon: true
                });
        }
    }

    const trackShipment = () => {
        AppModelService.createPopUp({
            compoent: <AppShipmentTrack />,
            headerName: '',
            header: false,
            footer: false,
            width: 738,
            height: 418,
            class: 'main_content',
            showCloseIcon: true
        }, showShipmentPreview);
    };

    const initiateRefund = () => {
        AppModelService.createPopUp({
            compoent: <AppInitiateRefund />,
            headerName: '',
            header: false,
            footer: false,
            width: 738,
            height: 418,
            class: 'main_content',
            showCloseIcon: true
        }, initiateRefundCallBack);
    };

    const initiateRefundCallBack = () => {
        props.initiateRefundClick();
    };

    const showShipmentPreview = () => {
        props.trackShipmentClick();
    }

    return (
        <>
            <div className='orderView'>
                <div className='order_content'>
                    <div className='profile'>
                        <div className='detail'>
                            <Avatar className='profileIcon' src={user.profilePic} alt={user?.name}></Avatar>
                            <p className='name regular-18'>{user?.name}</p>
                        </div>
                        <div onClick={(e) => openAction(e)}>
                            <MoreVertIcon className='three_dots' />
                        </div>
                    </div>
                    <div className='order_cell'>
                        <div className='order_detail_container'>
                            <p className='option regular-18'>
                                <span className='primary_text_color'>Query: </span>
                                <span className='quinary_text_color'>{query.question}</span>
                            </p>
                            <div>
                                <label>
                                    <span className='primary_text_color'>Description: </span>
                                    <span className='quinary_text_color'>{detail}</span>
                                </label>
                            </div>
                            <div>
                                <label className='attachment_label' onClick={() => showPreviewPopup(attachments)}>Show Attachments</label>
                            </div>
                        </div>
                    </div>
                </div>
                <div className='order_action_container'>
                    <div className="order_action_grid">
                        <div className="button-wrapper"><button className='action_button' onClick={trackShipment}>Track Shipment</button></div>
                        <div className="button-wrapper"><button className='action_button' onClick={initiateRefund}>Initiate Payment</button></div>
                        <div className="button-wrapper"><button className='action_button'>Initiate Insurance</button></div>
                    </div>
                </div>
            </div>
            <PopoverComponent open={popoverOpen} anchorEl={pAnchorEl} closePopover={closePopover} configData={configData} selectedData={selectedData} />
        </>
    );
}

export default AppOrderReportView;